<template>
  <div class="hello">
    <h3>人數</h3>
    <button @click="PeopleNum--">-</button>
    <input type="text" v-model="PeopleNum" />
    <button @click="PeopleNum++">+</button>
    <b-button @click="CreateHandler()" class="btn btn-info mt-3">確認</b-button>
    <b-button @click="back()" class="btn btn-info mt-3">返回</b-button>
  </div>
</template>

<script>
import router from '@/router'
import { db } from '@/db'
import _ from 'lodash'
const fs = db

export default {
  name: 'Create',
  data: () => {
    return {
      PeopleNum: 4,
      CreateRoomID: []
    }
  },
  watch: {
    PeopleNum: function (NewVal, OldVal) {
      if (_.isNumber(NewVal)) {
        if (NewVal < 4 || NewVal > 12) {
          this.PeopleNum = OldVal
        }
      } else {
        this.PeopleNum = OldVal
      }
    }
  },
  methods: {
    CreateHandler: _.debounce(function () {
      const roomId = '' + _.random(1000, 9999)
      console.log(this.PeopleNum, roomId)
      fs.doc(roomId).get().then(doc => {
        if (!doc.data()) {
          fs.doc(roomId).set({
            num: this.PeopleNum
          }).then(() => {
            router.replace({ path: `/room/${roomId}` })
          })
        } else {
          this.CreateHandler()
        }
      })
    }, 500),
    back: () => {
      router.replace({ path: '/' })
    }
  },
  mounted: function () {
    this.$bind(
      'CreateRoomID',
      fs
    )
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h3 {
  margin: 40px 0 0;
}
input {
  font-size: 24px;
  text-align: center;
  width: 36px;
}
i {
  font-size: 36px;
  width: 30px;
  height: 30px;
  margin: auto 20px;
}
.btn {
  display: block;
  margin: 15px 0;
  width: 100%;
}
</style>
